export default {
	data() {
		return {
			schoolInfo:null,
			pageNum:1,
			loadModal:false
		}
	},
	methods: {
		loadData()
		{
			var me = this
			uni.request({
				url: this.serverUrl + '/led/school/' + this.pageNum + '?name=&limit=1',
				method: 'GET',
				data: {},
				success: res => {
					
					if(this.schoolInfo!=null && this.schoolInfo.id == res.data[0].id)
					{
						uni.showToast({
							title: "到底了!"
						})
						this.pageNum--
						this.loadModal = false
						return
					}
					
					
					me.schoolInfo = res.data[0]
					this.loadModal = false
				},
				fail: () => {},
				complete: () => {}
			});
		},
		prevent()
		{
			if(this.pageNum>1)
			{
				this.pageNum--
				this.loadModal = true
				this.loadData()
			}
		},
		next()
		{
			this.loadModal = true
			this.pageNum++
			this.loadData()
		},
		showLedgerAndTeacherInfo()
		{
			uni.navigateTo({
				url: "../ledger_school_detail/ledger_school_detail?schoolId=" + this.schoolInfo.id
			})
		}
	},
	onLoad() {
		this.loadData()
	}
}
